import axios from "axios"
import Vuex from "vuex"
import Vue from 'vue'
import Auth from './auth'
import info from './info'
const baseUrl = "http://localhost:3000/newClothes"

Vue.use(Vuex)

export default new Vuex.Store({
    data () {
        return {
          newClothes: []
        }
    },
    async created() {
        try{
          const res = await axios.get(baseUrl);
    
          this.state.clothe = res.data;
        } catch (e) {
          console.error(e);
        }
    },
    state: {
        clothe: [],
        error: null,
        basket: []
    },
    getters: {
        clothe: s => s.clothe,
        clotheById: s => id => s.clothe.find(c => c.id === id),
        error: s => s.error
    },
    modules: {
      Auth, info
    },
    mutations: {
      setError(state, error) {
        state.error = error
      },
      clearError(state) {
        state.error = null
      },
      SET_BASKET: (state, product) => {
        if(state.basket.length) {
           let isProdictExists = false;
           state.basket.map(function (item) {
               if(item.article === product.article) {
                   isProdictExists = true;
                   item.quantity++
               }
           })
           if(!isProdictExists) {
               state.basket.push(product);
           }
        }
        else {
           state.basket.push(product);
        }
      }
    },
    actions: {
      ADD_TO_BASKET({commit}, product) {
          commit('SET_BASKET', product)
      },
      BASKET(state) {
        return state.basket;
      }
    }
})